import { useState } from 'react';
import { Card, CardContent, CardMedia, Typography } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import defaultImage from '../assets/noimg.jpg';
const CardProyect = ({ name, text, img, title, proyect, subtitle }) => {
    const [shadow, setShadow] = useState(2);
    console.log(img);
    return (
        <Card
            elevation={shadow}
            style={{ height: 250, display: 'flex', borderRadius: 15 }}
            onMouseEnter={() => setShadow(5)}
            onMouseLeave={() => setShadow(1)}
        >
            <CardMedia
                image={!img ? defaultImage : img}
                title={name}
                style={{ width: '55%', margin: 5, borderRadius: 15 }}
            />

            <CardContent
                style={{
                    width: '45%',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between',
                    overflow: 'auto'
                }}
            >
                <Typography
                    style={{ fontSize: isMobile ? 10 : 16, letterSpacing: 1.4 }}
                >
                    {title}
                </Typography>
                <TypografyCustom style={{ fontSize: isMobile ? 10 : 12 }}>
                    {subtitle}
                </TypografyCustom>

                <Typography style={{ fontSize: isMobile ? 10 : 14 }}>
                    {proyect}
                </Typography>
            </CardContent>
        </Card>
    );
};

const TypografyCustom = ({ children }) => {
    let txt = children;
    if (children && children.length > 156) {
        txt = txt.slice(0, 147);
        txt = `${txt} ...leer mas`;
    }
    return (
        <Typography style={{ fontSize: isMobile ? 10 : 12 }}>{txt}</Typography>
    );
};

export default CardProyect;
