import React, { useState } from 'react';

import {
    Paper,
    Grid,
    Typography,
    makeStyles,
    Card,
    CardMedia
} from '@material-ui/core';
import provStyles from '../../styles/provStyle';
import { incluirData } from '../../info/provincia';
import { isMobile } from 'react-device-detect';

const VacunasContent = () => {
    const classes = provStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item sm={12}>
                    <Card elevation={5} className={classes.cards}>
                        <CardMedia />
                    </Card>
                </Grid>
                <Grid item sm={8}>
                    <Grid container spacing={3}>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.text}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.card0.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card0.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.card1.txtA}
                                </Typography>

                                <Typography className={classes.textmed}>
                                    {incluirData.card1.txtC}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card1.txtD}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.card3.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card3.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item sm={4}>
                    <Grid container spacing={3}>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.card2.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card2.txtB}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card2.txtC}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card2.txtD}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.card4.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card4.txtB}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card4.txtC}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card4.txtD}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {incluirData.card4.txtE}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    );
};

export default VacunasContent;
