import React, { useContext } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {
    Card,
    CardContent,
    CardHeader,
    CardMedia,
    Fab,
    Typography,
    makeStyles,
    IconButton
} from '@material-ui/core';
import colores from '../styles/colors';
import ArtTrackIcon from '@material-ui/icons/ArtTrack';
import CardNoticia from '../components/cardNoticia';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { FormContext } from '../context/formContext';

export default function NoticiasView() {
    const classes = useStyles();
    const { noticias } = useContext(FormContext);
    const [open, setOpen] = React.useState(false);
    const [clickNota, setClickNota] = React.useState(false);

    const descriptionElementRef = React.useRef(null);
    React.useEffect(() => {
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);

    return (
        <div className={classes.prensaDiv}>
            <Fab
                size="large"
                variant="extended"
                className={classes.prensaBtn}
                onClick={() => setOpen(true)}
            >
                <ArtTrackIcon className={classes.prensaIco} />
                Prensa
            </Fab>

            <Dialog
                maxWidth="md"
                open={open}
                onClose={() => setOpen(false)}
                scroll={'paper'}
            >
                <DialogTitle id="prensa">
                    {clickNota ? (
                        <>
                            <IconButton
                                edge="start"
                                color={colores.mainDarkfull}
                                onClick={() => setClickNota(false)}
                                aria-label="close"
                            >
                                <ArrowBackIcon />
                            </IconButton>
                            {clickNota.title}
                        </>
                    ) : (
                        'Ultimas Noticias'
                    )}
                </DialogTitle>
                <DialogContent dividers={true}>
                    <DialogContentText
                        id="notas"
                        ref={descriptionElementRef}
                        tabIndex={-1}
                    >
                        <div
                            style={{
                                marginRight: 30,
                                marginLeft: 30
                            }}
                        >
                            {!clickNota ? (
                                noticias.map((nota) => (
                                    <CardNoticia
                                        data={nota}
                                        setClickNota={setClickNota}
                                    />
                                ))
                            ) : (
                                <NoticiaOn data={clickNota} />
                            )}
                        </div>
                    </DialogContentText>
                </DialogContent>
                <DialogActions /* style={{ margin: 'auto' }} */>
                    <Button color="primary">Todo</Button>
                    <Button color="primary">Medio Ambiente</Button>
                    <Button color="primary">Genero y Diversidad</Button>
                    <Button color="primary">Barrios y Obras</Button>
                    <Button color="primary">Prevencion Covid-19</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

const NoticiaOn = ({ data }) => {
    const classes = useStyles();

    return (
        <>
            <Typography>{data.subtitle}</Typography>
            <CardMedia image={data.img} className={classes.media} />
            <Typography>{data.text}</Typography>
        </>
    );
};

