import { useState } from 'react';
import {
    Card,
    CardActions,
    CardContent,
    CardHeader,
    CardMedia,
    makeStyles,
    Typography
} from '@material-ui/core';
import colores from '../styles/colors';
import noimg from '../assets/noimg.jpg';

const CardNoticia = ({ data, setClickNota }) => {
    const [shadow, setShadow] = useState(0);
    const classes = useStyles();

    return (
        <Card
            onClick={() => setClickNota(data)}
            elevation={shadow}
            className={classes.cardd}
            onMouseEnter={() => setShadow(3)}
            onMouseLeave={() => setShadow(0)}
        >
            <CardHeader
                title={data.title.slice(0, 81)}
                subheader={data.subtitle.slice(0, 99)}
            />

            <CardMedia image={data.img || noimg} className={classes.mediaimg} />
            <CardContent>
                <Typography className={classes.letratxt}>
                    {data.text.slice(0, 240)}
                </Typography>
            </CardContent>
            <CardActions
                style={{
                    display: 'flex',
                    flexDirection: 'row-reverse',
                    marginRight: 20
                }}
            >
                <Typography> {data.date.slice(0, 10)}</Typography>
            </CardActions>
        </Card>
    );
};

const useStyles = makeStyles(() => ({
    letratxt: {
        marginBottom: 10
    },
    cardd: {
        marginTop: 30,
        backgroundColor: colores.mainLight,
        width: 600,
        margin: 'auto',
        cursor: 'pointer',
        overflow: 'auto'
    },
    mediaimg: {
        height: 240
    }
}));

export default CardNoticia;
