import React, { useState } from 'react';

import {
    Paper,
    Grid,
    Typography,
    makeStyles,
    CardMedia,
    Card
} from '@material-ui/core';
import { begData } from '../../info/provincia';
import { isMobile } from 'react-device-detect';
import provStyles from '../../styles/provStyle';
import begBanner from '../../assets/ejesProv/beg/begBanner.jpeg';
import jorBeg from '../../assets/jor/jorBeg.jpg';

const BilleteraContent = () => {
    const [select, setSelect] = useState('');
    const classes = provStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid sm={12}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? '80%' : 180,
                            borderRadius: 15,
                            width: isMobile ? '90%' : null,
                            margin: 'auto',
                            marginTop: 10
                        }}
                    >
                        <img
                            src={jorBeg}
                            style={{
                                width: '100%',
                                marginTop: isMobile ? '-20%' : '-30%'
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={7}>
                    <Grid container spacing={isMobile ? 2 : 3}>
                        <Grid item xs={12} sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textTitle}>
                                    {begData.text}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={6}>
                            <Paper elevation={3} className={classes.cardMini}>
                                <Typography className={classes.textmed}>
                                    {begData.card1.txtB}
                                </Typography>
                            </Paper>
                        </Grid>

                        <Grid item xs={6} sm={6}>
                            <Paper elevation={3} className={classes.cardMini}>
                                <Typography className={classes.textmed}>
                                    {begData.card1.txtC}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={3}>
                    <Paper elevation={3} className={classes.cardVert}>
                        <Typography className={classes.textBig}>
                            {begData.card2.txtA}
                        </Typography>
                        <Typography className={classes.textmed}>
                            {begData.card2.txtB}
                        </Typography>
                    </Paper>
                    <Card
                        elevation={select !== 'solicitar' ? 3 : 5}
                        onMouseEnter={() => setSelect('solicitar')}
                        onMouseLeave={() => setSelect('')}
                        className={classes.media}
                        onClick={() => window.open(begData.card4.url)}
                    >
                        <CardMedia
                            image={begData.card4.img}
                            style={{ height: '100%' }}
                        />

                        <Typography className={classes.textmed}>
                            {begData.card0.txtB}
                        </Typography>
                    </Card>

                    <Card
                        elevation={select !== 'app' ? 3 : 5}
                        className={classes.media}
                        onMouseEnter={() => setSelect('app')}
                        onMouseLeave={() => setSelect('')}
                        onClick={() => window.open(begData.card5.url)}
                    >
                        <CardMedia
                            image={begData.card5.img}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
                <Grid xs={0} item sm={2}>
                    <Card
                        elevation={5}
                        style={{ height: '100%', borderRadius: 15 }}
                    >
                        <CardMedia
                            image={begBanner}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

export default BilleteraContent;
