import Axios from 'axios';

class ProyectoService {
    get token() {
        return localStorage.getItem('token') || '';
    }

    get headers() {
        return {
            headers: {
                token: this.token
            }
        };
    }

    async getProyectos() {
        return await Axios.get(`${process.env.REACT_APP_URL}/proyecto`).then(
            ({ data }) =>
                data.noticias.filter((noticias) => noticias._id).reverse()
        );
    }

    async crearProyecto(noticia) {
        return await Axios.post(
            `${process.env.REACT_APP_URL}/proyecto`,
            noticia,
            this.headers
        )
            /* .then((res) => res.json()) */
            .then((res) => res);
    }

    borrarProyecto(_id) {
        const url = `${process.env.REACT_APP_URL}/proyecto/${_id}`;
        return Axios.delete(url, this.headers)
            .then(() => alert('Borrado', '', 'success'))
            .catch((err) => console.log(err));
    }

    async actualizarProyecto(id, newNoticia) {
        return await Axios.put(
            `${process.env.REACT_APP_URL}/proyecto/${id}`,
            newNoticia,
            this.headers
        ).catch((e) => console.log(e));
    }

    async getNoticiaById(id) {
        return await Axios.get(
            `${process.env.REACT_APP_URL}/proyecto/${id}`
        ).catch((e) => console.log(e));
    }
}

export default new ProyectoService();
