import { makeStyles } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import colores from './colors';

const provStyles = makeStyles(() => ({
    icon: {
        height: 26,
        width: 26,
        marginRight: 10
    },
    cardCenter: {
        height: isMobile ? 120 : 140,
        padding: isMobile ? 10 : '18px 14px',
        backgroundColor: 'rgba(21, 162, 209,.4)',
        overflow: 'auto',
        //letterSpacing: 1.2,
        textAlign: 'center',
        borderRadius: 15,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        color: colores.darkBlueNav
    },
    cards: {
        height: 140,
        padding: isMobile ? 10 : 18,
        backgroundColor: 'rgba(21, 162, 209,.4)',
        overflow: 'auto',
        letterSpacing: 1.4,
        textAlign: isMobile && 'center',
        borderRadius: 15,
        color: colores.darkBlueNav
    },
    cardMini: {
        height: 120,
        padding: isMobile ? 10 : 18,
        backgroundColor: 'rgba(21, 162, 209,.4)',
        overflow: 'auto',
        letterSpacing: 1.4,
        textAlign: isMobile && 'center',
        borderRadius: 15,
        color: colores.darkBlueNav
    },
    cardVert: {
        height: 98,
        borderRadius: 15,
        overflow: 'auto',
        marginBottom: 15,
        padding: isMobile ? 10 : 16,
        backgroundColor: '#0472c2',
        color: '#f2f2f2',
        cursor: 'pointer'
    },
    textGrl: {
        fontSize: isMobile ? 14 : 22
    },
    textBig: {
        fontSize: isMobile ? 22 : 24,
        letterSpacing: 1.2,
        marginBottom: !isMobile && 3
    },
    textmed: {
        fontSize: isMobile ? 12 : 14,
        letterSpacing: 1.2
    },
    textTitle: {
        fontSize: isMobile ? 16 : 18,
        letterSpacing: 1
    },
    media: {
        cursor: 'pointer',
        height: 91,
        borderRadius: 15,
        marginBottom: 16
    },
    '@global': {
        '*::-webkit-scrollbar': {
            width: '0em'
        }
    }
}));

export default provStyles;
