import React, { useState } from 'react';

import {
    Paper,
    Grid,
    Typography,
    makeStyles,
    Card,
    CardMedia
} from '@material-ui/core';
import provStyles from '../../styles/provStyle';
import { vacunasData } from '../../info/provincia';
import { isMobile } from 'react-device-detect';
import jorVac1 from '../../assets/jor/jorVacunas1.jpg';
import jorVac2 from '../../assets/jor/jorVacunas2.jpg';

const VacunasContent = () => {
    const classes = provStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid xs={12} sm={10}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? 140 : 180,
                            borderRadius: 15,
                            width: isMobile ? '90%' : null,
                            margin: 'auto',
                            marginTop: 10
                        }}
                    >
                        <img
                            src={jorVac1}
                            style={{
                                width: '100%',
                                marginTop: isMobile ? '-20%' : '-30%',
                                paddingTop: '10%'
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={2}>
                    <Paper
                        elevation={3}
                        className={classes.cardVert}
                        style={{
                            height: '100%',
                            textAlign: 'center'
                        }}
                        onClick={() =>
                            window.open(
                                'https://www.santafe.gob.ar/santafevacunacovid/inicio'
                            )
                        }
                    >
                        <Typography
                            className={classes.textmed}
                            style={{ marginBottom: 10 }}
                        >
                            {vacunasData.card0.txtA}
                        </Typography>
                        {isMobile && vacunasData.card0.ico}
                        <Typography className={classes.textGrl}>
                            {!isMobile && vacunasData.card0.ico}
                            {vacunasData.card0.txtB}
                        </Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12} sm={10}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={8}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textTitle}>
                                    {vacunasData.text}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textTitle}>
                                    {vacunasData.card2.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {vacunasData.card2.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={6}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textTitle}>
                                    {vacunasData.card5.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {vacunasData.card5.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={6}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textTitle}>
                                    {vacunasData.card1.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {vacunasData.card1.txtB}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {vacunasData.card1.txtC}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item sm={2}>
                    <Card
                        elevation={5}
                        style={{ height: '100%', borderRadius: 15 }}
                    >
                        <CardMedia image={jorVac2} style={{ height: '100%' }} />
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

export default VacunasContent;
