import {
    Grid,
    TextField,
    Paper,
    Card,
    TextareaAutosize,
    Button,
    Typography
} from '@material-ui/core';
import { useState } from 'react';
import defaultImage from '../../assets/noimg.jpg';
import ProyectoService from '../../helpers/proyectoService';
import { actualizarArchivo } from '../../helpers/fileUpload';
import CardProyect from '../cardProyect';

const ProyectTab = () => {
    return (
        <>
            <FormularioProyectos />
        </>
    );
};

const FormularioProyectos = () => {
    const [nota, setNota] = useState({
        title: '',
        subtitle: '',
        type: '',
        text: '',
        img: ''
    });

    return (
        <Grid container spacing={3}>
            <Grid item sm={6}>
                <Grid
                    container
                    direction="column"
                    alignItems="center"
                    justify="center"
                >
                    {keys.map((dta) => {
                        if (dta.type === 'area')
                            return (
                                <TextField
                                    onChange={(e) =>
                                        setNota({
                                            ...nota,
                                            [dta.key]: e.target.value
                                        })
                                    }
                                    value={nota[dta.key]}
                                    variant="filled"
                                    id={dta.key}
                                    label={dta.key}
                                    multiline
                                    rows={4}
                                    fullWidth
                                    style={{ marginBottom: 20 }}
                                />
                            );
                        return (
                            <TextField
                                onChange={(e) =>
                                    setNota({
                                        ...nota,
                                        [dta.key]: e.target.value
                                    })
                                }
                                value={nota[dta.key]}
                                size="small"
                                variant="filled"
                                type={dta.type === 'date' && 'date'}
                                id={dta.key}
                                label={dta.type !== 'date' && dta.key}
                                fullWidth
                                style={{ marginBottom: 20 }}
                            />
                        );
                    })}
                </Grid>
            </Grid>
            <Grid item sm={6}>
                <ImageModal nota={nota} />
                <CardProyect {...nota} />
            </Grid>
        </Grid>
    );
};

const ImageModal = ({ nota }) => {
    const [file, setFile] = useState();
    const [imagenn, setImagenn] = useState();

    function validateForm() {
        return (
            nota.title.length > 0 &&
            nota.text.length > 0 &&
            nota.subtitle.length > 0 &&
            file != null
        );
    }

    function mostrarImagen(file) {
        setFile(file);
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = (e) => {
            setImagenn(reader.result);
        };
    }

    async function handleSubmit() {
        await ProyectoService.crearProyecto(nota)
            .then(async (res) => {
                console.log('resp ', res);
                // const id = res.noticia._id;
                /*  const result = await actualizarArchivo(file, 'noticia', id);
                if (result) return alert('nota creada con exito');
                else return alert('error al crear la nota'); */
            })
            .catch((err) => {
                console.log(err.response);
                alert('error al cargar la nota');
            });
    }

    return (
        <Grid
            container
            spacing={3}
            direction="column"
            style={{ paddingRight: 50, paddingLeft: 50 }}
        >
            <Typography
                style={{ marginTop: 20, marginBottom: 20, textAlign: 'center' }}
            >
                Seleccionar Imagen
            </Typography>
            <Button
                variant="contained"
                component="label"
                sty={{ width: 100, height: 160 }}
            >
                {imagenn ? (
                    <img
                        className="shadow mt-3"
                        style={{ height: '150px', width: '240px' }}
                        src={imagenn}
                        alt="imk1"
                    />
                ) : (
                    <img
                        className="shadow mt-3"
                        style={{ height: '150px', width: '240px' }}
                        src={defaultImage}
                        alt="imk2"
                    />
                )}
                <input
                    onChange={(e) => {
                        mostrarImagen(e.target.files[0]);
                    }}
                    type="file"
                    hidden
                />
            </Button>
            <Button
                onClick={() => {
                    handleSubmit();
                }}
                style={{ marginTop: 20 }}
                //disabled={!validateForm()}
                fullWidth
                color="primary"
                variant="contained"
            >
                {' '}
                Cargar noticia
            </Button>
        </Grid>
    );
};

const keys = [
    { key: 'title', type: 'normal' },
    { key: 'subtitle', type: 'normal' },
    { key: 'text', type: 'area' },
    { key: 'proyect', type: 'normal' }
];

export default ProyectTab;
